class Arrow {
    constructor(el, params) {
        this.$el = el instanceof $ ? el : $(el);
        this.params = $.extend(true, {}, Arrow.DEFAULTS, params);
        this.s = Snap(this.$el.find('svg')[0]);
        this.paths = this.s.selectAll('path');
        this._prepareForDrawing();
        if (this.params.shadow) this._dropShadow();
        this.$el.data('arrow', this);
    }

    draw() {
        const { duration, easing } = this.params;
        this.paths.forEach((path, i) => {
            setTimeout(() => {
                path.animate({ strokeDashoffset: 0 }, duration, easing);
            }, duration * i);
        });
    }

    reset() {
        const { duration, easing } = this.params;
        this.paths.items.slice().reverse().forEach((path, i) => {
            const len = path.getTotalLength();
            setTimeout(() => {
                path.animate({ strokeDashoffset: len }, duration, easing);
            }, duration * i);
        });
    }

    _prepareForDrawing() {
        this.s.attr('height', '');
        this.paths.forEach(path => {
            const len = path.getTotalLength();
            path.attr({
                stroke: '#333',
                strokeDasharray: len,
                strokeDashoffset: len
            });
        });
    }

    // _init(fragment) {
    //     this.s = Snap(fragment.node);
    //     this.paths = this.s.selectAll('path');
    //     this._prepareForDrawing();
    //     if (this.params.shadow) this._dropShadow();
    //     this.s.appendTo(this.$el[0]);
    //     console.log(this);
    // }

    _dropShadow() {
        const { dx, dy, blur, color, opacity } = this.params.shadowParams;
        const vb = this.s.attr('viewBox');

        const offsetX = 2 * blur;
        const offsetY = 2 * blur;
        const _w      = vb.width + offsetX;
        const _h      = vb.height + offsetY;
        const shiftX  = dx >= blur ? 0 : blur - dx;
        const shiftY  = dy >= blur ? 0 : blur - dy;

        this.s.attr({
            viewBox: `${vb.x} ${vb.y} ${_w} ${_h}`,
            width: _w
        });

        this.shadow = this.s.filter(
            Snap.filter.shadow(dx, dy, blur, color, opacity)
        );

        this.s.select('g').attr({
            transform: `translate(${shiftX} ${shiftY})`
        });

        this.paths[0].attr({
            filter: this.shadow
        });
    }
}

Arrow.DEFAULTS = {
    duration: 500,
    easing: mina.easein,
    shadow: true,
    shadowParams: { dx: 0, dy: 20, blur: 10, color: 'black', opacity: 0.3 }
};
